import React from 'react';
import Link from 'next/link';
import Head from 'next/head';

import { replace, throwException, trim, parseUniformConfig } from '@uniformdev/common';

import { getPageUrl } from '@uniformdev/common-client';
import { UniformConfig } from '@uniformdev/common';

import { getNextConfig } from '..';

export class SmartLink extends React.Component<{ href: string; className?: string; attribs?: any }> {
    render() {
        const config: UniformConfig = parseUniformConfig(getNextConfig());
        const prefetch = config.UNIFORM_OPTIONS_PREFETCH_LINKS;
        let { href, className, attribs } = this.props;
        attribs = attribs || {};

        if (className) {
            attribs.className = className;
        }

        if (attribs.href) {
            delete attribs.href;
        }

        if (attribs['class']) {
            delete attribs['class'];
        }

        const children: any = this.props.children || throwException('no props.children');
        if (!href) {
            return <a {...attribs}>{children}</a>;
        }
        if (href && !href.startsWith('/')) {
            return (
                <Link href={href}>
                    <a {...attribs}>{children}</a>
                </Link>
            );
        }

        const value = trim(href, '/');
        const link = replace(`/${value}/`, '//', '/');

        const newHref = '/index';

        const as = link;
        if (!prefetch) {
            return (
                <Link href={newHref} as={as}>
                    <a {...attribs}>{children}</a>
                </Link>
            );
        }

        const prefetchPage = !prefetch ? undefined : getPageUrl(href, 'page', config);
        // TODO: not possible to prefetch datasources this way
        //const prefetchDatasources = getPageUrl(href, 'ds', config);
        //const prefetchHtml = !prefetch ? undefined : getPageUrl(href, 'html', config);

        return (
            <>
                <Link href={newHref} as={as}>
                    <a {...attribs}>{children}</a>
                </Link>
                <Head>
                    <link
                        rel="preload"
                        crossOrigin="anonymous"
                        href={prefetchPage}
                        as="fetch"
                        key={prefetchPage}
                    />
                    {/* <link
                        rel="preload"
                        crossOrigin="anonymous"
                        href={prefetchDatasources}
                        as="fetch"
                        key={prefetchDatasources}
                    /> */}
                    {/* <link
                        rel="preload"
                        crossOrigin="anonymous"
                        href={prefetchHtml}
                        as="fetch"
                        key={prefetchHtml}
                    /> */}
                </Head>
            </>
        );
    }
}
